import React, {
  DetailedHTMLProps,
  InputHTMLAttributes,
  HTMLAttributes,
  useState,
} from 'react'
import s from './SuperEditableSpan.module.css'
import editIcon from './editIcon.svg'
import { SuperInput } from '../SuperInput/SuperInput';
import SuperButton from '../SuperButton/SuperButton';

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement>
//    
type DefaultSpanPropsType = DetailedHTMLProps<HTMLAttributes<HTMLSpanElement>,
  HTMLSpanElement>

//               ,  type
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperEditableSpanType = Omit<DefaultInputPropsType, 'type'> & {
  //  +       
  onChangeText?: (value: string) => void
  onEnter?: () => void
  error?: string

  spanProps?: DefaultSpanPropsType & { defaultText?: string }//   
}

export const SuperEditableSpan: React.FC<SuperEditableSpanType> = (
  {
    autoFocus,
    onBlur,
    onEnter,
    spanProps,
    error,

    ...restProps //       restProps
  }
) => {
  const [editMode, setEditMode] = useState<boolean>(false)
  const { children, onDoubleClick, className, defaultText, ...restSpanProps } =
    spanProps || {}

  const onEnterCallback = () => {
    //  editMode   Enter //  
    if (!error) {
      setEditMode(!editMode)
      onEnter?.()
    }
  }
  const onBlurCallback = (e: React.FocusEvent<HTMLInputElement>) => {
    //  editMode      //  
    if (!error) {
      setEditMode(!editMode)
      onBlur?.(e)
    }
  }
  const onDoubleClickCallBack = (e: React.MouseEvent<HTMLSpanElement, MouseEvent>) => {
    //  editMode    //  
    setEditMode(!editMode)
    onDoubleClick?.(e)
  }

  const spanClassName = s.span
    + (className ? ' ' + className : '')

  return (
    <>
      {editMode ? (
        <div className={s.inputBlock}>
          <SuperInput
            autoFocus={autoFocus || true}
            onBlur={onBlurCallback}
            onEnter={onEnterCallback}
            className={error ? s.errorInput : s.input}
            label={''}
            labelClassName={s.label}
            {...restProps} //        (value   )
          />
          <SuperButton disabled={!!error} className={s.inputButton}>save</SuperButton>
        </div>
      ) : (
        <div className={s.spanBlock} onDoubleClick={onDoubleClickCallBack}>
          <span
            className={spanClassName}
            {...restSpanProps}
          >
            {/*     ,   */}
            {children || restProps.value || defaultText}
          </span>
          <img
            src={editIcon}
            className={s.pen}
            alt={'edit'}
          />
        </div>
      )}
    </>
  )
}